

import doctorOrOrg from '../../components/DoctorOrOrg';
import moduleTitle from '../../components/ModuleTitle';
import tabBar from '../../components/TabBar'
import searchBar from '../../components/SearchBar';
import tools from '../../lib/tools.js';
import Toast from 'vux/src/components/toast/index.vue'

import BScroll from 'better-scroll'
export default {
name:'doctorList',
components:{doctorOrOrg,moduleTitle,Toast,tabBar,searchBar},
data(){
return {
id:'',
showToast:false,
toastMsg:'',
tempService:[],
pageNumber:1,
pageSum:0,
container:null,
dragging: false,
times: 0,
loading: false,
loadable:false,
pullUpObj:{
type:0,
text:''
},// 0  1 2
pickerSortArr:[],//['','','','',''],
pickerContentArr:[],//['','','',''],
dictionary:[],
searchObj:{},
}
},
mounted(){


















this.init();
this.initDictionary();
},
beforeDestory() {













},
methods:{
init(){
var vm = this;
console.log('init');


let data = {pageSize:10,pageNumber:this.pageNumber,roleId:1,userStatus:1,shieldJY:1};
if(Object.keys(this.searchObj).length>0){
for(let key in this.searchObj){
data[key] = this.searchObj[key];
}
}
this.$http.post('/backstage/query',data).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.list && data.list.length==0){
vm.showToast = true;
vm.toastMsg = '';
if(vm.pullUpObj.type == 0){
vm.tempService = [];
}
return ;
}
var tempArr = [];
for(let i=0;i<data.data.list.length;i++){
let item = data.data.list[i];
tempArr.push({src:item.imageUrl,title:item.name,desc:item.skills,url:'/m/doctorIndex.html?md='+item.managerId})
}
this.pageSum = data.data.pageSum;
this.loading = false;
this.loadable = (this.pageSum>1);
if(this.pullUpObj.type==0){
vm.tempService = tempArr;
vm.pullUpObj.type = 1;
}else{
vm.tempService = [...vm.tempService,...tempArr];
}
}else{
this.loading = false;
this.loadable = false;
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
this.loading = false;
this.loadable = false;
console.log(error);
vm.showToast = true;
vm.toastMsg = '';
})
},
touchDown(e) {

this.pageY = e.changedTouches ? e.changedTouches[0].pageY : e.pageY;

},
touchMove(e){
const container = this.container;
const pageY = e.changedTouches ? e.changedTouches[0].pageY : e.pageY;
const moveY = pageY - this.pageY;

if (moveY < 0 && (container.scrollTop + Math.min(global.innerHeight,container.clientHeight)) >= container.scrollHeight) {
e.preventDefault()
this.dragging = true;
if (moveY < -80 && this.dragging) {
this.pageY = pageY;
if(this.pageSum==this.times){
this.started = false;
this.loading = false;
this.loadable = false;
this.dragging = false;
}else{
this.times += 1;
this.started = true;
this.loading = true;
this.loadable = true;
this.pageNumber += 1;
this.init();
}
}
}
},
touchUp(e){
this.dragging = false
},
nextPage(){
if(!(this.pageSum==this.times)){
this.times += 1;
this.pageNumber += 1;
console.log('nextPage');
this.init();
}else{
this.pullUpObj.type = 2;
this.pullUpObj.text = ''
}
},
reLoad(){
this.pullUpObj = {
type:0,
text:''
};
this.times = 0;
this.pageNumber = 1;
this.init();
},
search(value){
console.log('-=-=',value);
this.searchObj = {name:value.value,type:value.type!=''?value.type:''};

this.$set(this.pullUpObj,'type',0);
this.pageNumber = 1;
this.pageSum = 0;
this.times = 0;
this.init();
},
initDictionary(){
let vm = this;

this.$http.get('/api/contracte/queryDictionary?typeId=31',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
this.dictionary = data.data;
for(let obj of data.data){
this.pickerSortArr.push({itemId:obj.itemId+'',itemName:obj.itemName});
}
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = '';
})
}
}
}



